@import url("https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700&family=Nunito:wght@200&family=Playfair+Display&display=swap");

$color-1: #756858; // Dark tan
$color-2: #0D1F2D; // Dark Navy
$color-3: #550527; // Red
$color-4: #A6988D; // light tan
$color-5: #D4CAC4; // lighter tan

$primary-font: 'Playfair Display', sans-serif;
$secondary-font: 'Nunito', sans-serif;
$p-font: 'Cinzel Decorative', serif;

@mixin hover {
        color: lighten($color-3, 30%);
        text-decoration: underline;
}


@mixin link {
    color: black;
    text-decoration: none;
}

@mixin border {
    padding-top: 1em;
    border-top: solid black .1em;
}
body{
    font-family: $secondary-font;
    font-weight: bold;
    background: url('images/background.jpg') repeat;

}

h2, h3{
    font-family: $primary-font;
    font-weight: bolder;
}

a{
        @include link;

    &:hover {
        @include hover;
    }
}
h1,
h2{
    text-align: center;
}

.page-wrapper {
    display: grid;
    grid-template-columns: auto;
}

section.intro h1 {
    display: block;
    text-transform: uppercase;
    font-family: $p-font;
    font-size: 4em;
    border-bottom: solid $color-1 2px;
    margin-top: 0;
    grid-row: 1/2;
    grid-column: 2/7;
}

