// }

.intro h2{
    font-size: 2em;
    padding: 0;
    margin: 0;
    margin-top: -1.3em;
    margin-bottom: -.4em;
}


#zen-summary p:nth-child(1) {
    font-weight: bold;
    font-size: 1.5em;
    padding-bottom: 20px;
    border-bottom: solid black 1px;
    //  margin-top: -100px;
}

@keyframes scroll {
    from {
      transform: translateX(100%);
    }
    to {
      transform: translateX(-100%);
    }
}

#zen-summary p:nth-child(2) {
    margin: -20px 0px 0px 0px;
    padding: 0;
    text-align: center;
    transform: translateX(50%);
    animation: scroll 20s linear infinite;
    overflow: hidden;
    //  margin-top: -100px;
}

#zen-preamble {
    border-top: solid black 1px;
    padding-top: 20px;
   
}

h3{
    margin-top:0;
}

#zen-preamble p,
#zen-requirements p {
    margin-top: -15px;
}
#zen-summary{
    overflow: hidden;
}
#zen-intro::after {
    content: '';
    display: block;
    /* width: 100%; */
    height: 30em;
    /* margin: 0 auto -26em; */
    margin: 2em 1em;
    background: url('images/paperman-large.jpg') no-repeat center;

}

#zen-participation::after {
    content: '';
    display: block;
    /* width: 100%; */
    height: 26em;
    /* margin: 0 auto -26em; */
    margin: 2em 4em;
    background: url('images/clock-small.jpg') no-repeat center;

}

#zen-explanation,
#zen-benefits {
    color: $color-5;
    padding: 10px;
    padding-top: 1px;
    background-color: $color-2;
}

#design-selection h3 {
    background-color: $color-1;
    padding: 1em;
    margin-bottom: -1em;
    text-align: center;
}

#design-selection ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
    text-decoration: none;
    list-style-type: none;
    background-color: $color-2;
    color: $color-5;
    padding: 1em;
    column-gap: 2em;
    row-gap: 2em;

    &a {
        text-decoration: none;
        
    }
}

#design-selection li {
    display: grid;
    grid-template-rows: 1fr .5em 1fr;
    grid-gap: .5em;
    background-color: $color-5;
    color: $color-2;
    text-align: center;

    & a:nth-child(1) {
        background-color: $color-4;
        text-align: center;
    }
}


.designer-name {
    grid-row: 3/4;
    text-align: center;
}

#design-archives {
    background-color: $color-2;
    color: $color-5;
    padding: 1em .5em;
    margin-top: -1em;

    & h3 {
        background-color: $color-4;
        color: black;
        padding: .5em;
        text-align: center;
    }

    & ul {
        display: flex;
        list-style-type: none;
        text-decoration: none;
        justify-content: space-around;
    }

    & a {
        text-decoration: none;
        color: $color-5;
    }
    & a:hover{
        @include hover;
    }

}

#zen-resources::before {
    content: '';
    display: block;
    /* width: 100%; */
    height: 26em;
    /* margin: 0 auto -26em; */
    margin: 1em 1em;
    background: url('images/carman-small.jpg') no-repeat center;
}

#zen-resources h3{
    padding: 1em;
    margin-bottom: 0;
}

#zen-resources ul {
    margin-top: 0;
    padding-left: 20px;
    & a {
        margin-top: -1em;
    }


    & li {
        list-style-type: none;
    }
}

.extra1{
    background-image: url(images/local_7.svg);
    height: 20px;
    background-repeat: repeat;
    background-position: center;
    margin-bottom: 20px;
}
@media only screen and (min-width: 600px) {
    .page-wrapper {
        display: grid;
        grid-template-rows: repeat(3, auto);
    }

    //header wrapper
    #zen-intro {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        grid-template-rows: repeat(4, auto);
        row-gap: 0;
        padding: 2em 3em;
        grid-row: 1/2;
    }

    #zen-intro header {
        grid-column: 1/7;
        grid-row: 1/2;
    }

    //demo
    #zen-summary {
        max-width: 43vw;
        grid-column: 1/4;
        grid-row: 2/3;

    }

    //road
    #zen-preamble {
        grid-column: 1/4;
        grid-row: 3/5;
        max-width: 43vw;
        columns: auto 7rem;
    }

    #zen-intro::after {
        grid-column: 4/7;
        grid-row: 2/5;
    }

    //second wrapper
    #zen-supporting {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-template-rows: repeat(3, auto);
        row-gap: 1.5em;
        padding: 2em 3em;
        grid-row: 3/4;
        column-gap: 2em;
    }

    //so what is this about
    #zen-explanation {
        grid-column: 1/4;
        grid-row: 1/2;
        background-color: transparent;
        color: black;
        max-width: 50vw;
        column-count: auto;
        column-width: 7rem;
    }

    //participation
    #zen-participation {
        grid-column: 4/7;
        grid-row: 3/4;
        max-width: 35vw;
        column-count: auto;
        column-width: 6rem;
        @include border;
    }


    //benefits
    #zen-benefits {
        grid-column: 4/7;
        grid-row: 1/2;
        background-color: white;
        color: black;

    }

    //requirements
    #zen-requirements {
        grid-row: 2/4;
        grid-column: 1/4;
        max-width: 50vw;
        column-count: auto;
        column-width: 7rem;
        @include border;
    }

    //html, css
    footer {
        grid-column: 4/6;
        grid-row: 2/3;
        justify-self: center;
        align-self: center;
        font-size: 1.5em;
        text-align: center;

    }

    #zen-participation::after {
        display: none;
    }

    #zen-benefits::after {
        content: '';
        display: block;
        height: 10em;
        margin: 0 1em 0em 1em;
        background: url('images/carman-large.jpg') no-repeat center;
        // grid-column: 4/7;
        // grid-row: 1/2;
    }

    //last wrapper
    aside .wrapper {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(1, auto);
        grid-row: 2/3;
    }

    //select a design
    #design-selection {
        grid-column: 2/3;
        grid-row: 1/2;
        margin: 0;
        padding: 0;
        background-color: $color-2;

        & h3 {
            background-color: #756858;
            padding: 10px;
            margin: 0;
        }

        & ul {
            padding: 10px;
            margin-top: 0px;
        }
    }


    //archives
    #design-archives {
        grid-column: 3/4;
        grid-row: 1/2;
        margin: 0;
        padding: 0;

        & h3 {
            background-color: #756858;
            padding: 10px;
            margin: 0;
        }

    }

    //resources
    #zen-resources {
        grid-column: 1/2;
        grid-row: 1/2;
        background-color: $color-4;

    }

    #zen-resources::before {
        display: none;
    }

    #zen-resources::after {
        content: '';
        display: block;
        /* width: 100%; */
        height: 26em;
        /* margin: 0 auto -26em; */
        margin: 1em 1em;
        background: url('images/typewriter-small.jpg') no-repeat center;
        grid-column: 1/2;
        grid-row: 2/3;
    }

    #design-archives::after {
        content: '';
        display: block;
        /* width: 100%; */
        height: 26em;
        /* margin: 0 auto -26em; */
        margin: 1em 1em;
        background: url('images/camera-large.jpg') no-repeat center;
        grid-column: 3/4;
        grid-row: 2/3;
    }
    #zen-explanation,
    #zen-benefits {
        background-color: transparent;
    }

}


